import { Column, Entity, PrimaryGeneratedColumn } from 'typeorm';
import { ItemStatus } from '../items/item-status.enum';

@Entity({ name: 'item' })
export class Item {
    @PrimaryGeneratedColumn('uuid')
    id: string;

    @Column()
    name: string;

    @Column()
    price: number;

    @Column()
    description: string;

    @Column({
        type: 'enum',
        enum: ItemStatus
      })
    status: ItemStatus;

    @Column()
    createdAt: string;

    @Column()
    updatedAt: string;
}